// jest.setup.js

// ðŸ‘‡ Mock NativeAnimatedHelper pour Ã©viter les erreurs liÃ©es Ã  Animated dans les tests
jest.mock('react-native/Libraries/Animated/NativeAnimatedHelper');

import { NativeModules } from 'react-native';

// ðŸ§  AsyncStorage
jest.mock('@react-native-async-storage/async-storage', () =>
  require('@react-native-async-storage/async-storage/jest/async-storage-mock')
);

// ðŸ§  Expo-font (Ã©vite les erreurs de fonts dans les tests)
jest.mock('expo-font', () => ({
  useFonts: () => [true],
  isLoaded: () => true,
}));

// ðŸ§  Expo-haptics (mock vibration)
jest.mock('expo-haptics', () => ({
  impactAsync: jest.fn(),
}));

// ðŸ§  react-native-reanimated
jest.mock('react-native-reanimated', () => {
  const Reanimated = require('react-native-reanimated/mock');
  Reanimated.default.call = () => {};
  return Reanimated;
});

// ðŸ§  NativeSettingsManager (Ã©vite l'erreur TurboModuleRegistry)
jest.mock('react-native/Libraries/Settings/Settings', () => ({
  get: jest.fn(() => 'light'),
  addEventListener: jest.fn(),
  removeEventListener: jest.fn(),
}));

// ðŸ§  NativeModules.SettingsManager (simule la locale)
NativeModules.SettingsManager = NativeModules.SettingsManager || {
  settings: { AppleLocale: 'en_US' },
};

// ðŸ§  Polyfill setImmediate (souvent manquant en test)
if (typeof setImmediate === 'undefined') {
  global.setImmediate = (fn, ...args) => setTimeout(fn, 0, ...args);
}
if (typeof clearImmediate === 'undefined') {
  global.clearImmediate = (id) => clearTimeout(id);
}

// ðŸ§  Mock complet pour react-native Animated (inclut Value, timing, interpolate...)
jest.mock('react-native/Libraries/Animated/Animated', () => {
  return {
    Value: jest.fn().mockImplementation((val) => ({
      _value: val,
      setValue: jest.fn(),
      addListener: jest.fn(),
      removeListener: jest.fn(),
      removeAllListeners: jest.fn(),
      interpolate: jest.fn(() => ({
        interpolate: jest.fn(),
      })),
    })),
    View: jest.fn(),
    timing: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    spring: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    decay: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    sequence: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    parallel: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    event: jest.fn(),
  };
});
